import angular from 'angular';
import Service from './service';
import collectTpl from './template/collectPerInfo.html';
import collectCtrl from './template/collectPerCtrl';
import passpwordTpl from './template/password.html';
import passwordCtrl from './template/passwordCtrl';
import messageTpl from './template/message.html';
import messageCtrl from './template/messageCtrl';
import utils from './../../common/utils.js';
import Configs from '../../app/config.js';

export default class MenuCtrl {
  constructor($http, $location, $rootScope, $timeout, $q, $interval, $ccModal, $ccValidator, $ccTips) {
    const _this = this;
    $rootScope.shuyunLoginUrl = '';
    _this.shopInfoDefer = $q.defer();
    $rootScope.user = _this.user = {
      'name': _this.userName,
      'noticeCount': 58,
      'id': _this.useID
    };
    // _ati
    Service.getAti({}, function() {
      // doing
    });

    // 
    Service.getShopInfo({}, function(data) {
      $rootScope.ccmsVersion = data.version_code;
      _this.shopInfoDefer.resolve();
      if (data.overdue) {
        _this.isExpire = true;
      }
      /* number tip */
      Service.getNumber({}, function(data) {
        _this.unionMarketingNumberShow = ((data.count) * 1) || false;
        _this.unionMarketingNumber = ((data.count) * 1) > 100 ? 99 : data.count;
      }, function(err) {
        _this.unionMarketingNumberShow = false;
        return err;
      });
    }, angular.noop);

    // 
    function checkLogin() {
      Service.checkLogin({}, function(data) {
        // 
        if (data.expired) { // true 
          var loginUrl;
          if (__pro__) {
            loginUrl = '/portal/timeout.html';
          } else {
            loginUrl = '/portal/login.html';
          }
          location.replace(loginUrl);
        } else {
          _this.userName = data.username;
          _this.useID = data.userId;
          _this.campUserName = data.username;
          _this.user.name = _this.userName;
          _this.user.id = _this.useID;
          $rootScope.tenantId = data.tenantId;
          window.sessionStorage.setItem('tenantId', data.tenantId);
          fetchMenu();
          getContactAjaxMethod();
          _this.getAccountLink();
        }
      }, function(data) {
        var loginUrl;
        if (__pro__) {
          loginUrl = '/portal/timeout.html';
        } else {
          loginUrl = '/portal/login.html';
        }
        location.replace(loginUrl);
      });
    }

    checkLogin();

    // 
    var clearStorage = function() {
      window.sessionStorage.removeItem('shuyunSessionMenu');
      window.sessionStorage.removeItem('shuYunMenuHelp');
      window.sessionStorage.removeItem('shuYunSSOlink');
      window.sessionStorage.removeItem('shuyunAccountLink');
      window.sessionStorage.removeItem('shuyunContact');
    };
    _this.logout = function() {
      var modalInstance = $ccModal.confirm('');
      modalInstance.open().result.then(() => {
        Service.logOut({}, function() {
          clearStorage();
          if (__pro__) {
            location.replace('/portal/loginout.html');
          } else {
            location.replace('/portal/login.html');
          }
        }, angular.noop);
      });
    };
    // 
    _this.changePassword = function() {
      $ccModal
        .modal({
          title: '',
          locals: {
            data: [1, 2, 3]
          },
          style: {
            'min-height': '140px',
            'min-width': '630px'
          },
          body: passpwordTpl,
          controller: passwordCtrl,
          controllerAs: 'passWordModal',
          bindings: true
        })
        .open();
    };

    function fetchMenu() {
      var navAry = [];
      var appSeeionMenu = angular.fromJson(sessionStorage.getItem('shuyunSessionMenu')); //  ''
      if (appSeeionMenu) {
        getSessionMenu(appSeeionMenu);
      } else {
        Service.checkVisit({
          hostName: $rootScope.hostName,
          tenantId: $rootScope.tenantId
        }, function(data) {
          angular.forEach(data.children, function(n) {
            n.url = root + n.url;
            navAry.push(n);
          });
          _this.user.nav = navAry;
          var stringData = JSON.stringify({
            menuData: _this.user.nav.slice()
          });
          setTimeout(function() {
            utils.setNavFunction();
          }, 0);
          sessionStorage.setItem('shuyunSessionMenu', stringData);
        }, function() {

        });
      }
    }

    function getSessionMenu(data) {
      var appSeeionMenu = data;
      _this.user.nav = appSeeionMenu.menuData.slice();
      setTimeout(function() {
        utils.setNavFunction();
      }, 0);
    }

    /* start  */
    var getContactAjaxMethod = function() {
      var shuyunContact = window.sessionStorage.getItem('shuyunContact');
      if (shuyunContact) {
        var response = JSON.parse(shuyunContact);
        if (response.exists) {
          _this.addPerInfoFlag = false;
          _this.contactLiFlag = true;
          _this.contactName = response.contact.fullName;
          _this.contactQQ = response.contact.qq;
          _this.contactPhone = response.contact.mobile;
          _this.contactWW = response.contact.wangwang;
          _this.contactEmail = response.contact.email;
          _this.contactWeibo = response.contact.weibo;
        } else {
          _this.addPerInfoFlag = true;
          _this.contactLiFlag = false;
          _this.contactName = '';
          _this.contactQQ = '';
          _this.contactPhone = '';
          _this.contactWW = '';
          _this.contactEmail = '';
          _this.contactWeibo = '';
        }
      } else {
        $http.get(Configs.webDashboard + '/dashboard/assemble/contact/exists/?tenantId=' + $rootScope.tenantId).success(function(response) {
          var Stringres = JSON.stringify(response);
          window.sessionStorage.setItem('shuyunContact', Stringres);
          if (response.exists) {
            _this.addPerInfoFlag = false;
            _this.contactLiFlag = true;
            _this.contactName = response.contact.fullName;
            _this.contactQQ = response.contact.qq;
            _this.contactPhone = response.contact.mobile;
            _this.contactWW = response.contact.wangwang;
            _this.contactEmail = response.contact.email;
            _this.contactWeibo = response.contact.weibo;
          } else {
            _this.addPerInfoFlag = true;
            _this.contactLiFlag = false;
            _this.contactName = '';
            _this.contactQQ = '';
            _this.contactPhone = '';
            _this.contactWW = '';
            _this.contactEmail = '';
            _this.contactWeibo = '';
          }
        });
      }
    };
    /* end  */

    _this.getContactInfo = function() { // 
      getContactAjaxMethod();
    };

    _this.cancelAddInfo = function() { // 
      _this.addPerInfoFlag = false;
    };

    _this.viewAddPerPop = function() { // 
      var modalInstance = $ccModal
        .modal({
          title: '',
          style: {
            'min-height': '240px'
          },
          body: collectTpl,
          controller: collectCtrl,
          controllerAs: 'collectModal',
          bindings: true
        })
        .open();

      modalInstance.result.then(function(v) {
        _this.addPerInfoFlag = false;
        _this.contactLiFlag = true;
      }, function(v) {

      });
    };

    // 
    _this.getHelpLink = function() {
      $http.get(Configs.webDashboard + '/dashboard/assemble/helplink/').success(function(response) {
        _this.helplink = response.url;
        window.open(_this.helplink);
      }).error(function(data, status, headers, config) {
        if (data.message) {
          $ccTips.error(data.message, document.querySelector('.content'));
        }
      });
    };
    // 
    _this.SSOlinkyes = false;
    _this.get5xLink = function() {
      var shuYunSSOlink = window.sessionStorage.getItem('shuYunSSOlink');
      if (shuYunSSOlink) {
        _this.SSOlink = shuYunSSOlink;
        _this.SSOlinkyes = true;
      } else {
        $http.get(Configs.webDashboard + '/dashboard/sso/directive/').success(function(resoponse) {
          if (resoponse) {
            _this.SSOlink = resoponse.url;
            _this.SSOlinkyes = true;
            window.sessionStorage.setItem('shuYunSSOlink', resoponse.url);
          }
        }).error(function(data, status, headers, config) {
          if (data.message) {
            $ccTips.error(data.message, document.querySelector('.content'));
          }
        });
      }
    };
    _this.get5xLink();

    // 
    _this.getAccountLink = function() {
      var accountlink = window.sessionStorage.getItem('shuyunAccountLink');
      if (accountlink) {
        _this.accountLink = accountlink;
      } else {
        $http.get(Configs.webDashboard + '/dashboard/channel/accountlink/?tenantId=' + $rootScope.tenantId).success(function(response) {
          _this.accountLink = response.url;
          window.sessionStorage.setItem('shuyunAccountLink', response.url);
        }).error(function(data, status, headers, config) {
          if (data.message) {
            $ccTips.error(data.message, document.querySelector('.content'));
          }
        });
      };
    };

    //  
    $rootScope.resertTemplateClickFlag = true;
    _this.disponseTitleALink = function(data) { //  $watch click();
      if (data.indexOf('callcenter') !== -1) {
        return 'javascript:void(0)';
      };

      if (data.indexOf('insert') === -1) {
        return data;
      } else {
        return 'javascript:void(0)';
      }
    };

    _this.appOrigin = location.protocol + '// ' + location.host; //  http
    _this.appRoot = root;

    _this.insertCompileLocation = function(url) {
      $rootScope.resertTemplateClickFlag = false;
      if (url.indexOf('callcenter') !== -1) { //  
        var newUrl = url.substring(8);
        window.open(newUrl);
        return false;
      }

      if (url.indexOf('insert') !== -1) {
        window.location.href = _this.appOrigin + url;
        if (window.location.href.indexOf('insert') !== -1) {
          window.location.reload();
        }
      }
    };

    // 
    _this.showMoreMessageDialog = function() {
      $ccModal
        .modal({
          title: '',
          hasFooter: false,
          body: messageTpl,
          style: {
            'min-height': 'auto',
            'max-height': '580px'
          },
          controller: messageCtrl,
          controllerAs: 'messageModal',
          bindings: {
            campUserName: _this.campUserName
          }
        })
        .open();
    };

    // 
    var interProm = null;
    var getMessageStatus = function() {
      var param = {
        'userName': $rootScope.tenantId,
        'ccmsVersion': $rootScope.ccmsVersion
      };
      Service.getStatus(param, function(res) {
        if (res.status) {
          _this.newsTrue = true;
        } else {
          _this.newsTrue = false;
        }
      }, function() {
        interProm && $interval.cancel(interProm);
      });
    };

    interProm = $interval(function() {
      getMessageStatus();
    }, 60000);

    _this.shopInfoDefer.promise.then(function() {
      getMessageStatus();
    });
  }
};
MenuCtrl.$inject = ['$http', '$location', '$rootScope', '$timeout', '$q', '$interval', '$ccModal', '$ccValidator', '$ccTips'];



