import Service from './../service';

export default ['$element', '$scope', '$rootScope', 'modalInstance', '$ccValidator', '$http', '$ccTips', '$interval', '$sce', function($element, $scope, $rootScope, modalInstance, $ccValidator, $http, $ccTips, $interval, $sce) {
  const that = this;
  const ccmsVersion = $rootScope.ccmsVersion;
  // 
  that.hideContentDialog = function() {
    that.noticeObj.hideMessContent();
  };

  this.noticeObj = {
    'deleIdList': {},
    'hideContentDialog': function() {
      that.isShowMessContent = false;
    },
    'setSelectAll': function(checked) {
      var _this = this;
      that.popMesses.forEach(function(item) {
        _this.deleIdList[item.id] = checked;
        item.select = checked;
      });
      that.deleteButton = checked;
    },
    'selectNoticeId': function(id, checked) {
      var _this = this;
      _this.deleIdList[id] = checked;
      var checkedList = that.popMesses.filter(function(item) {
        return item.select === true;
      });
      that.deleteButton = checkedList.length > 0;
      that.selectAll = checkedList.length === that.popMesses.length;
      if (that.popMesses.length === 0) {
        that.selectAll = false;
      }
    },
    'getNoticeCb': function(res) {
      that.selectAll = false;
      // 
      var pages = Math.ceil(res.total / 10);
      if (pages === 0) {
        pages = 1;
      }
      res.data.forEach(function(item) {
        item.isRead = item.status === 1;
        // item.select = that.noticeObj.deleIdList[item.noticeId];
      });
      that.deleteButton = false;

      that.popMesses = res.data;
     // that.page = res.page;
      that.total = pages;
      // 
      that.isLastPage = !!(pages - res.page);
      that.isFirstPage = res.page !== 1;
    },
    'getNoticeEB': function(res) {
      that.popMesses = [];
      that.page = 0;
      that.total = 0;
    },
    onPageChange: function(pageNum) {
      var _this = this;
      var param = {
        'userName': $rootScope.tenantId,
        'ccmsVersion': ccmsVersion,
        'pageNo': pageNum
      };
      that.page = pageNum;
      Service.getNoticesList(param, _this.getNoticeCb, _this.getNoticeEB);
    },
    'getNotices': function() {
      var _this = this;
      var param = {
        'userName': $rootScope.tenantId,
        'ccmsVersion': ccmsVersion,
        'pageNo': 1
      };
      that.page = 1;
      Service.getNoticesList(param, _this.getNoticeCb, _this.getNoticeEB);
    },
    'readClick': function(index) {
      var notice = that.popMesses[index];
      var param = {
        'campUserName': that.campUserName,
        'userName': $rootScope.tenantId,
        'ccmsVersion': ccmsVersion,
        'id': notice.noticeId * 1
      };
      // $('#isShowMessContent').css('display', 'block')
      that.isShowMessContent = true;
      notice.isRead || Service.postNotice(param);
      notice.isRead = true;

      that.content = $sce.trustAsHtml(notice.content);
      that.contitle = notice.title;
      that.condate = notice.created;
    },
    'deleteNotice': function() {
      if (!that.deleteButton) return;
      var _this = this;
      var ids = [];
      for (var i in _this.deleIdList) {
        if (_this.deleIdList[i]) {
          ids.push(i);
        }
      }
      var param = {
        'userName': $rootScope.tenantId,
        'setId': ccmsVersion,
        'ccmsVersion': ccmsVersion,
        'ids': ids.join(',')
      };

      var cb = function(res) {
        _this.ids = [];
        var text = '';
        if (res.success) {
          text = '';
        }
        _this.getNotices();
        $ccTips.success(text, document.querySelector('.ccms-modal'));
      };
      var eb = function() {
      };

      Service.deleteNotices(param, cb, eb);
    },
    'clearNotice': function() {
      var _this = this;
      var ids = [];
      that.popMesses.forEach(function(item) {
        if (item.isRead) {
          ids.push(item.noticeId);
        }
      });
      if (ids.length === 0) return;
      var param = {
        'userName': $rootScope.tenantId,
        'setId': ccmsVersion,
        'ccmsVersion': ccmsVersion,
        'ids': ids.join(',')
      };

      var cb = function(res) {
        _this.ids = [];
        _this.getNotices();
        $ccTips.success('', document.querySelector('.ccms-modal'));
      };
      var eb = function() {
      };

      Service.deleteNotices(param, cb, eb);
    }
  };

  // 
  this.noticeObj.getNotices();
}];



