import genResource from 'angular-es-utils/rs-generator';
import Configs from '../../app/config.js';

export default {
  checkVisit(param, success, error) {
    const VisitResource = genResource(Configs.webPortal + '/visits', false);
    VisitResource.save(param, success, error);
  },
  checkLogin(param, success, error) {
    const CheckResource = genResource(Configs.webPortal + '/credentials', false);
    CheckResource.get(param, success, error);
  },
  logOut(param, success, error) {
    const LoginResource = genResource(Configs.webPortal + '/loginOut', false);
    LoginResource.delete(param, success, error);
  },
  getShopInfo(param, success, error) {
    const ShopResource = genResource(Configs.webDashboard + '/dashboard/assemble/shopinfo/', false);
    ShopResource.get(param, success, error);
  },
  getNumber(param, success, error) {
    const EbmResource = genResource(Configs.webNode + '/node/edm/cb-service/messages/count/?_=' + new Date().getTime(), false);
    EbmResource.get(param, success, error);
  },
  getNoticesList(param, success, error) {
    const userName = param.userName;
    const ccmsVersion = param.ccmsVersion;
    const pageNo = param.pageNo;
    const timestamp = new Date().getTime();
    const pageSize = param.pageSize || 10;
    const NoticesResource = genResource(Configs.webPortal + '/notification/notice' + '?timestamp=' + timestamp + '&pageSize=' + pageSize + '&pageNo=' + pageNo + '&setId=' + ccmsVersion + '&userName=' + userName, false);
    NoticesResource.get({}, success, error);
  },
  getStatus(param, success, error) {
    const userName = param.userName;
    const ccmsVersion = param.ccmsVersion;
    const StatusResource = genResource(Configs.webPortal + '/notification/status?setId=' + ccmsVersion + '&userName=' + userName, false);
    StatusResource.get({}, success, error);
  },
  postNotice(param, success, error) {
    const userName = param.userName;
    const ccmsVersion = param.ccmsVersion;
    const campUserName = param.campUserName;
    const id = param.id;
    const data = {
      'setId': ccmsVersion,
      'userName': userName,
      'noticeId': id,
      'optName': campUserName
    };
    const NoticeResource = genResource(Configs.webPortal + '/notification/notice/' + id, false);
    NoticeResource.save(data, success, error);
  },
  deleteNotices(param, success, error) {
    const userName = param.userName;
    const ccmsVersion = param.setId;
    const ids = param.ids;
    const NoticeResource = genResource(Configs.webPortal + '/notification/notice?setId=' + ccmsVersion + '&userName=' + userName + '&noticeId=' + ids, false);
    NoticeResource.delete({}, success, error);
  },
  getAti(param, success, error) {
    var ati = '';
    var cookiesAry = document.cookie.split(';');
    cookiesAry.forEach(function(v, i) {
      if (v.indexOf('_ati') !== -1) {
        ati = v.replace(/\s/g, '').substr(5);
      }
    });
    const ShopResource = genResource(Configs.webDashboard + '/dashboard/assemble/setLoginLog?ati=' + ati, false);
    ShopResource.get(param, success, error);
  }
};



