import Configs from '../../../app/config.js';

export default ['$element', '$scope', '$http', 'modalInstance', '$ccValidator', '$ccTips', function($element, $scope, $http, modalInstance, $ccValidator, $ccTips) {
  const that = this;
  this.ok = function() {
    that.perInfoSave(modalInstance.ok);
  };

  this.ccValidators = {
    phone: {
      msg: '',
      fn: (modelValue, viewValue) => {
        const value = modelValue || viewValue;
        return value ? /^[1][3-9]{1}\d{9}$/.test(value) : !value;
      }
    }
  };

  this.perInfoSave = function(callback) { // 
    $ccValidator.validate($scope.collerinfo).then(() => {
      var contactPerInfo = {};
      contactPerInfo.fullName = that.contactName;
      contactPerInfo.email = that.contactEmail;
      contactPerInfo.mobile = that.contactPhone;
      contactPerInfo.qq = that.contactQQ;
      contactPerInfo.wangwang = that.contactWW;
      contactPerInfo.weibo = that.contactWeibo;
      contactPerInfo.tenantId = $scope.tenantId;
      $http.post(Configs.webDashboard + '/dashboard/assemble/contact/', contactPerInfo).success(function(response) {
        if (response.code === 200) {
          var sessionData = window.sessionStorage.getItem('shuyunContact');
          if (!sessionData) {
            sessionData = '{}';
          }
          sessionData = JSON.parse(sessionData);
          sessionData.exists = true;

          for (var i in contactPerInfo) {
            if (!sessionData.contact) {
              sessionData.contact = {};
            }
            sessionData.contact[i] = contactPerInfo[i];
          }
          sessionData = JSON.stringify(sessionData);
          window.sessionStorage.setItem('shuyunContact', sessionData);
          callback();
          $ccTips.success('');
        } else {
          $ccTips.error(response.message, document.querySelector('.content'));
        }
      });
    }, () => {
      console.log('!');
    });
  };
  /* end  */
}];



