import './insert.scss';
import angular from 'angular';
import {Inject} from 'angular-es-utils';

@Inject('$rootScope', '$stateParams', '$sce', '$state')
export default class IframeCtrl {
  constructor($rootScope, $stateParams, $sce, $state) {
    const _this = this;
    const context = $stateParams.context || '';
    angular.forEach($rootScope.user.nav, function(v, k) {
      if (v.url.indexOf('insert') !== -1 && v.id.indexOf('_' + context) !== -1) {
        var seesionItem = sessionStorage.getItem('marketingParams');
        var dataUrl = v.dataUrl;
        if (seesionItem) {
          dataUrl += ('&' + seesionItem);
        }
        console.log(dataUrl);
        _this.contextUrl = $sce.trustAsResourceUrl(dataUrl);
        return false;
      }
    });
    window.addEventListener('message', function(e) {
      // dashboard ==> marketing
      if (e.data) {
        if (e.data === 'authReject') {
          if (__pro__) {
            location.pathname = '/portal/timeout.html';
          } else {
            location.pathname = '/portal/login.html';
          }
        } else if (e.data === 'contentManage') {
          // 
          $state.go('insert', {context: 'contentManage'});
        }
      };
      window.removeEventListener('message', function() {}, false);
    }, false);
  }
};



