import angular from 'angular';
import ngResource from 'angular-resource';
import ngAnimate from 'angular-animate';
import ngSanitize from 'angular-sanitize';
import ccmsComponents from 'ccms-components';
import projectRouter from '../common/project-router/project.js';
import router from './router';
import commonDirectve from '../components';
import insert from './insert';
import Configs from './config';
import { setAuthFailedBehavior, setRefreshTokenUrl } from 'ccms-sdk/interceptors/token-refresh-interceptor';
import tokenRefreshInterceptor from 'ccms-sdk/interceptors/token-refresh-interceptor-jq';

setRefreshTokenUrl(Configs.passport + '/credentials/refresh');
setAuthFailedBehavior(function() {
  if (__pro__) {
    location.replace('/portal/timeout.html');
  } else {
    location.replace('/portal/login.html');
  }
});

window.$.ajaxSetup(tokenRefreshInterceptor);

var webApp = angular.module('ccmsApp', [projectRouter, ngAnimate, ngSanitize, ngResource, ccmsComponents, commonDirectve, insert]);
webApp.config(router);

webApp.run(['$rootScope', function($rootScope) {
  $rootScope.$on('$viewContentLoading', function(event, viewConfig) {
    $rootScope.tmlLoaded = false;
  });
  $rootScope.$on('$viewContentLoaded', function(event, viewConfig) {
    $rootScope.tmlLoaded = true;
  });
  $rootScope.$on('$stateChangeStart', function(e, toState, toParams, fromState, fromParams, options) {
    if (!options.isTrigger) {
      sessionStorage.removeItem('marketingParams');
    }
  });
}]);



