import insertTpl from './insert/insert.html';
import Configs from './config';
import tokenRefreshInterceptor, { setAuthFailedBehavior, setRefreshTokenUrl } from 'ccms-sdk/interceptors/token-refresh-interceptor';
setRefreshTokenUrl(Configs.passport + '/credentials/refresh');
setAuthFailedBehavior(() => {
  if (__pro__) {
    location.replace('/portal/timeout.html');
  } else {
    location.replace('/portal/login.html');
  }
});

export default function appRouter($projectProvider, $httpProvider) {
  $httpProvider.defaults.withCredentials = true;
  // interceptor
  $httpProvider.interceptors.push(() => tokenRefreshInterceptor);

  // 401
  $httpProvider.interceptors.push(['$q', function($q) {
    return {
      responseError: function(response) {
        if (response.status === 401) {
          if (__pro__) {
            location.replace('/portal/timeout.html');
          } else {
            location.replace('/portal/login.html');
          }
        };
        return $q.reject(response);
      }
    };
  }]);

  Object.freeze($httpProvider);

  $projectProvider
    .state('ebm', {
      url: '/ebm',
      templateUrl: '/ebm/index.html?_=' + TimeTemp
    })
    .state('wxcrm', {
      url: '/wxcrm',
      templateUrl: '/wxcrm-web/index.html?_=' + TimeTemp
    })
    .state('branding', {
      url: '/branding',
      templateUrl: '/branding/index.html?_=' + TimeTemp
    })
    .state('customer', {
      url: '/customer',
      templateUrl: '/customer/index.html?_=' + TimeTemp
    })
    .state('insert', {
      url: '/insert/{context}',
      templateUrl: insertTpl
    }).state('dashboard', {
      url: '/dashboard',
      templateUrl: '/ccms/modules/dashboard/index.html?_=' + TimeTemp
    }).state('campagin', {
      url: '/campagin',
      templateUrl: '/ccms/modules/marketing/index.html?_=' + TimeTemp
    }).state('systemManage', {
      url: '/systemManage',
      templateUrl: '/ccms/modules/systemManage/index.html?_=' + TimeTemp
    }).state('dataManagement', {
      url: '/dataManagement',
      templateUrl: '/ccms/modules/dataManagement/index.html?_=' + TimeTemp
    }).state('benefitManage', {
      url: '/benefitManage',
      templateUrl: '/ccms/modules/benefitManage/index.html?_=' + TimeTemp
    }).state('portlet', {
      url: '/portlet',
      templateUrl: '/portlet/index.html?_=' + TimeTemp
    });
};
appRouter.$inject = ['$projectProvider', '$httpProvider'];



